@media screen and (max-width: 767px) {
	html {
		font-size: 87%; }
	.burger>span {
		display: block;
		width: 1.4rem;
		height: .27rem;
		background-color: #000; }
	.burger {
		position: relative;
		display: block;
		width: 3rem;
		height: 3rem;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 10001; }

	.burger span:before {
		content: "";
		width: 1.9rem;
		height: 0.27rem;
		background-color: #000;
		position: absolute;
		display: block;
		transition: .3s;
		top: .8rem;
		left: .55rem; }
	.burger span:after {
		content: "";
		display: block;
		width: 1.9rem;
		height: 0.27rem;
		background-color: #000;
		position: absolute;
		transition: .3s;
		top: 1.96rem;
		left: .55rem; }
	.burger-active span:after {
		transform: rotate(45deg);
		transform-origin: right bottom;
		width: 1.67rem;
		background-color: rgba(246, 0, 6, 0.9); }
	.burger-active span:before {
		transform: rotate(-45deg);
		transform-origin: right top;
		width: 1.67rem;
		background-color: rgba(246, 0, 6, 0.9); }
	.burger-active span {
		height: 0;
		background-color: rgba(246, 0, 6, 0.9); }
	.nav__item {
		display: none; }
	.nav {
		display: flex;
		justify-content: flex-end; }
	.container-header {
		padding: .7rem .8rem .7rem .5rem; }
	.header-contacts {
		 display: none; }
	.header-top__container {
		display: flex;
		justify-content: center; }
	.header-btn {
		min-width: 20rem; }
	.section-second__item {
		max-width: 34rem;
		margin-bottom: 1.2rem; }
	.nav-right {
		display: block;
		width: 14rem;
		background-color: #ccc; }
	.nav-right-inner a {
		text-decoration: none;
		color: #000;
		text-align: center;
		color: red;
		margin-bottom: 1rem; }
	.nav-right-inner a:last-child {
		margin-bottom: 0rem; }
	.nav-right-inner {
		display: flex;
		flex-direction: column; }
	.nav-parent {
		display: flex;
		justify-content: flex-end;
		position: absolute;
		top: 9rem;
		right: -15rem;
		z-index: 2; }
	.nav-pop-up {
		width: 13rem;
		height: 35rem;
		background-color: red;
		display: block;
		transform: translate(90%,14%);
		z-index: -1;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: 1s;
		&:hover {
			transform: translateX(100%);
			transform: translateY(14%); } }
	.nav-pop-up-wrapper {
		display: flex;
		justify-content: flex-end;
		position: absolute;
		width: 100vw;
		z-index: 1000; }
	.nav-pop-up ul {
		display: flex;
		flex-direction: column; }
	.nav-pop-up ul li {
		padding-bottom: 1.5rem; }
	.nav-pop-up-wrapper {
		position: absolute; } }
